}
.full-screen::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  margin: auto;
  left: -50vw;
  right: -50vw;
  z-index: -2;
}
.full-screen-left::after {
  content: '';
  display: block;
  height: 100%;
  width: 50vw;
