.proposals__title {
  display: flex;
  flex-direction: column;
}
.icon-kard,
.icon-hammer {
  font-size: 38px;
  margin-right: 15px;
}
.proposals__headline {
  color: #333333;
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  margin-bottom: 0;
  margin-top: 4px;
  text-transform: uppercase;
}
.proposals__text {
  margin: 0 0 5px 0;
  color: #333333;
}
.section__main {
  display: flex;
  flex-direction: column;
}
.section-headline {
  border-top: 1px solid #c0c0c0;
  margin-bottom: 17px;
}
.section__headline {
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 18px;
  color: #333333;
}
.sections__item {
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 0 -15px 70px -15px;
}
.section__new,
.section__discounts {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  margin: 0 15px;
  position: relative;
  max-width: 212px;
}
.label {
  position: absolute;
  top: 0;
  left: 0;
  width: 54px;
  height: 54px;
  background-color: #ff8b00;
  border-radius: 0 0 54px 0;
  font-family: 'Ubuntu-Medium', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: #fffbf1;
  padding: 8px 0 0 5px;
  z-index: 2;
}
.lable__discounts {
  background-color: #900057;
  color: #c0c0c0;
}
.section-item__link {
  margin: 0;
}
.item__title {
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
  text-align: center;
  margin-top: 19px;
  margin-bottom: 0;
}
.item__cost {
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  font-size: 14px;
  text-align: center;
  margin-top: 3px;
  margin-bottom: 24px;
}
.section__form {
  display: flex;
  justify-content: flex-start;
  margin-top: auto;
  width: 100%;
  height: 35px;
}
.basket-new__button,
.basket-discounts__button {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24%;
  font-size: 25px;
  border-left: 1px solid #fffbf1;
  color: #fffbf1;
}
.new__button {
  background-color: #ff8b00;
}
.discounts__button {
  background-color: #900057;
  color: #c0c0c0;
}
.button__one-clik {
  -webkit-tap-highlight-color: #ff8b00;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 76%;
  background-color: #ff8b00;
  color: #fffbf1;
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  font-size: 14px;
}
.basket-new__button:hover,
.button__one-clik:hover {
  background-color: #ff8b00;
  color: #900057;
}
.basket-new__button:active,
.button__one-clik:active {
  background-color: transparent;
  color: #900057;
}
.basket-new__button:focus,
.button__one-clik:focus {
  background-color: #ff8b00;
  color: #900057;
}
.basket-new__button:disabled,
.button__one-clik:disabled {
  background-color: #ff8b00;
  color: #333333;
}
.button__discounts-figure {
  -webkit-tap-highlight-color: #900057;
  background-color: #900057;
  color: #c0c0c0;
}
.basket-discounts__button:hover,
.button__discounts-figure:hover {
  background-color: #900057;
  color: #fffbf1;
}
.basket-discounts__button:active,
.button__one-clik:active {
  background-color: #900057;
  color: #ff8b00;
}
.basket-discounts__button:focus,
.button__one-clik:focus {
  background-color: #900057;
  color: #ff8b00;
}
.basket-discounts__button:disabled,
.button__one-clik:disabled {
  background-color: #900057;
  color: #333333;
}
.footer__main {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 20px;
  margin-bottom: 50px;
  position: relative;
}
.footer-logo__pict {
  color: #c0c0c0;
}
.footer__link {
  align-self: flex-start;
}
.footer-menu__conteiner {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-top: 1px solid #c0c0c0;
  margin-top: 22px;
  margin-right: 30px;
  width: 20%;
  flex-wrap: nowrap;
  flex-grow: 1;
}
.footer__logo {
  border-top: 0;
  margin-top: 22px;
  margin-left: 0;
  flex: 0 4 40%;
}
.footer-menu__conteiner:last-child {
  margin-right: 0;
}
.footer-menu__title {
  font-family: Ubuntu-Light, sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 16px;
  color: #c0c0c0;
}
.footer__menu {
  background-color: transparent;
  flex-direction: column;
  padding-left: 0;
}
.footer-menu__item {
  display: flex;
  justify-content: flex-start;
}
.footer-menu__link {
  padding: 5px 0;
  font-size: 14px;
}
.footer__address {
  padding: 5px 0;
  font-style: normal;
  font-size: 14px;
  margin: 0;
  color: #c0c0c0;
  font-family: Ubuntu-Light, sans-serif;
  font-weight: 400;
}
.copyright {
  display: flex;
  height: 50px;
  background-color: #222121;
  margin: 0 -30px;
  align-items: center;
  position: relative;
}
.copyright::after {
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position: absolute;
  top: 0;
  margin: auto;
  left: -50vw;
  right: -50vw;
  z-index: -1;
}
.copyright__text {
  font-family: Ubuntu-Light, sans-serif;
  font-weight: 400;
  padding-left: 30px;
  color: #c0c0c0;
}
@media (max-width: 990px) {
  .header-main__connect {
    flex-direction: column;
  }
  .header-main-connect__tel,
  .header-main-connect__email {
    margin-top: 10px;
  }
  .form__search {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    padding-bottom: 30px;
  }
  .basket {
    margin-bottom: 30px;
  }
  .main__gray-back {
    height: 10px;
  }
  .aside__baner {
    display: none;
  }
  .aside {
    width: 10%;
  }
  .submenu-tab {
    display: none;
  }
  .aside__item {
    border-bottom: 0;
  }
  .aside__title {
    display: none;
  }
  .menu__ul {
    margin-top: 40px;
  }
  .main {
    width: 90%;
  }
}
@media (max-width: 800px) {
  .main__content {
    flex-direction: column-reverse;
  }
  .main {
    width: 100%;
  }
  .main__slider {
    margin: 0 -30px 0 -30px;
    width: auto;
    margin-bottom: 20px;
  }
  .main__gray-back {
    display: none;
  }
  .aside__menu {
    margin-bottom: 15px;
  }
  .menu__ul {
    display: flex;
    flex-direction: row;
    margin-top: 10px;
  }
  .icon_piano,
  .icon_trump,
  .icon_violin,
  .icon_drum,
  .icon_sheet_music,
  .icon_accessories {
    font-size: 27px;
    margin-right: 20px;
  }
}
@media (max-width: 767px) {
  .main-logo {
    width: 230px;
  }
  .header-main__logo {
    width: auto;
    margin-left: 0;
    flex-grow: 0;
  }
  .footer-logo__pict {
    width: 190px;
  }
  .header-main-center__content {
    flex-direction: column;
    justify-content: center;
  }
  .header-main__search {
    align-items: flex-end;
    align-self: flex-end;
    width: auto;
  }
  .header-main__connect {
    flex-direction: column;
    align-self: flex-end;
  }
  .header-main-connect__tel,
  .header-main-connect__email {
    margin-top: 10px;
    margin-right: 0;
    flex-direction: row-reverse;
    align-items: center;
    align-self: flex-end;
  }
  .form__search {
    width: 185px;
    padding-top: 10px;
    padding-bottom: 20px;
    align-self: flex-end;
  }
  .search__input {
    width: 137px;
  }
  .basket {
    margin-left: 0;
  }
  .header-main {
    flex-wrap: wrap;
  }
  .slick-arrow {
    display: none!important;
  }
}
@media (max-width: 754px) {
  .sections__item {
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 -15px 20px -15px;
  }
  .section__new,
  .section__discounts {
    margin: 0 15px 50px 15px;
  }
  .footer-menu__conteiner {
    width: auto;
    flex: 1 0 10%;
    margin-top: 75px;
  }
  .footer__logo {
    margin-top: -10px;
    position: absolute;
  }
  .footer-logo__pict {
    margin-right: 0;
    width: 160px;
  }
}
@media (max-width: 660px) {
  .proposals__content {
    flex-wrap: wrap;
    margin: 38px -15px 40px -15px;
  }
  .proposals__item {
    margin: 0 15px 30px 15px;
  }
  .proposals__item:last-child {
    margin: 0 15px 15px 15px;
  }
}
@media (max-width: 600px) {
  .slide__text {
    top: 15%;
    right: 6%;
  }
  .slide__headline {
    font-size: 47px;
  }
  .slide__p {
    font-weight: 400;
    font-size: 16.5px;
  }
  .section__new,
  .section__discounts {
    max-width: 37.2%;
  }
}
@media (max-width: 551px) {
  .header__main-menu {
    align-items: flex-start;
    flex-direction: column;
    position: relative;
  }
  .menu__main {
    display: flex;
    position: absolute;
    flex-direction: column;
    align-items: flex-start;
    top: 45px;
    width: 100%;
    z-index: 2;
    padding: 0;
    overflow: hidden;
    height: 0;
  }
  .menu__item {
    margin-left: 30px;
  }
  .menu__item:first-child {
    margin-top: 5px;
  }
  .menu__item:last-child {
    margin-bottom: 20px;
  }
  .footer__menu {
    display: flex;
    position: relative;
    padding: 0 0 0 0;
    top: 0;
    height: auto;
  }
  .footer-menu__item {
    margin-left: 0;
  }
  .footer-menu__item:last-child {
    margin-bottom: 0;
  }
  .footer-menu__item:first-child {
    margin-bottom: 0;
  }
  .hide__menu {
    display: flex;
    align-self: flex-start;
  }
  .slick-dots {
    bottom: -33.6px;
  }
  .footer-menu__conteiner:nth-child(3) {
    display: none;
  }
  .footer-menu__conteiner {
    margin-top: 100px;
  }
}
@media (max-width: 500px) {
  .header__main {
    flex-direction: column;
    align-items: center;
  }
  .header-main__logo {
    width: auto;
    margin-left: 0;
    margin-right: 0;
    flex-grow: 0;
    padding-top: 40px;
  }
  .span {
    display: none;
  }
}
@media (max-width: 450px) {
  .slide__text {
    top: 19%;
    right: 6%;
  }
  .slide__headline {
    font-size: 39px;
    text-align: right;
  }
  .slide__p {
    font-weight: 400;
    font-size: 15px;
  }
  .section__new,
  .section__discounts {
    max-width: 100%;
    border: 1px solid #c0c0c0;
  }
}
@media (max-width: 360px) {
  .slide__text {
    top: 27%;
    right: 9%;
  }
  .slide__headline {
    font-size: 32px;
  }
  .slide__p {
    display: none;
  }
}
