@import "variable";
.header__top-menu{
  display: flex;
  max-width: 1280px;
  justify-content: space-between;
  position: relative;
}

.header__top-menu-action{
  display: flex;
  flex-direction: row;
  margin-left: -@interval;
  background-color: @gray-color;
  padding: 8px 10px 8px @interval;
  border-radius: 0 0 45px 0;
  align-items: center;
}

.interval-icon{
  margin-right: 8px;
}

.top-menu-action__link{
  -webkit-tap-highlight-color: transparent;
  display: flex;
  align-items: center;
  color: @back-color;
}

.icon-entry, .icon-favourites, .icon_compare{
  width: 18px;
}

.top-menu-action__link:link {
  background-color: transparent;
  color: @back-color;
  fill: @back-color;
}

.top-menu-action__link:visited {
  background-color: transparent;
  color: @back-color;
  fill: @back-color;
}
.top-menu-action__link:hover {
  background-color: transparent;
  color: @main-color;
  fill: @main-color;
}

.top-menu-action__link:focus {
  background-color: transparent;
  color: @orange_color;
  fill: @orange_color;
}

.top-menu-action__link:active {
  color: @orange_color;
  fill: @orange_color;
}

.header__social-network{
  display: flex;
  flex-direction: row;
  align-items: center;
}

.icon-vk{
  font-size: 26px;
  margin-top: 6px;
  width: 26px;
  margin-right: 17px;
  margin-bottom: -7px;
}

.icon_fb{
  font-size: 26px;
  margin-top: 3px;
  width: 10px;
  margin-right: 14px;
  margin-bottom: -7px;
}
.icon_tw{
  font-size: 26px;
  margin-top: 5px;
  width: 22px;
  margin-bottom: -7px;
}

.social-network__link{
  -webkit-tap-highlight-color: transparent;
  color: @gray-color;
  margin-left: 14px;
  margin-right: 0;
}

.social-network__link:link{
  background-color: transparent;
  color: @gray-color;
}

.social-network__link:visited {
  background-color: transparent;
  color: @gray-color;
}
.social-network__link:hover {
  background-color: transparent;
  color: @main-color;
}

.social-network__link:focus {
  background-color: transparent;
  color: @orange_color;
}

.social-network__link:active {
  background-color: transparent;
  color: @orange_color;
}