@import "variable";
//header main content
.header-main-center__content{
  display: flex;
  flex-grow: 3;
}

.header-main__logo{
  display: flex;
  align-items: flex-start;
  padding: 22px 0 22px 0;
  margin-left: -5px;
  margin-right: 30px;
  width: 212px;
  flex-grow: 1;
  flex-shrink: 3;
}

.logo__link{
  display: flex;
  flex-wrap: nowrap;
  align-items: flex-start;
  justify-content: flex-start;
}

.main-logo{
  display: flex;
  flex-wrap: nowrap;
  align-self: center;
  font-size: 76px;
  color: @dark-color;
  margin-top: 0;
  width: 190px;
}



.header-main__search{
  display: flex;
  flex-direction: column;
  flex-grow: 3;
  flex-shrink: 0;
  justify-content: space-between;
  align-content: flex-start;
  padding: 12px 0 0 0;
  width: 205px;
}

.header-main__connect{
  display: flex;
  align-self: flex-start;
  padding-left: 5px;
  flex-wrap: wrap;
}

.header-main-connect__tel, .header-main-connect__email{
  display: flex;
  margin-right: 10px;
  color: @main-color;
}

.header-main__telefon, .header-main__mail{
  width: 20px;
  height: 20.07px;
}

.header-main__link{
  -webkit-tap-highlight-color: transparent;
  padding-left: 8px;
  font-style: normal;
  color: @dark-color;
}
.header-main__link:link {
  background-color: transparent;
  color: @dark-color;
}

.header-main__link:visited {
  background-color: transparent;
  color: @dark-color;
}

.header-main__link:hover {
  background-color: transparent;
  color: @main-color;
}

.header-main__link:focus {
  background-color: transparent;
  color: @orange_color;
}

.header-main__link:active {
  background-color: transparent;
  color: @orange_color;
}

.form__search{
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  padding-bottom: 47px;
  align-items: center;
}

.search__input{
  border: 1px solid @gray-color;
  width: 69.5%;
  padding-left: 10px;
  height: 37px;
}

.search__button{
  -webkit-tap-highlight-color: @main-color;
  display: flex;
  background-color: @main-color;
  border: 0;
  color: @gray-color;
  transition: .3s;
  justify-content: center;
  align-items: center;
  width: 47px;
  height: 37px;
}

.search__button:hover{
  background-color: @main-color;
  color: @back-color;
}

.search__button:active{
  background-color: @main-color;
  color: @orange_color;
}

.search__button:focus{
  background-color: @main-color;
  color: @orange_color;
}

.search__button:disabled{
  background-color: @main-color;
  color: @dark-color;
}

.icon_search{
  display: flex;
  font-size: 26px;
}

.basket{
  display: flex;
  flex-direction: row;
  align-self: flex-end;
  width: 185px;
  height: 37px;
  border: 1px solid @gray-color;
  justify-content: space-between;
  margin-left: 8%;
  margin-bottom: 48px;
}

.basket__cost{
  font-family: 'Ubuntu-Medium', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: @main-color;
  align-self: center;
  padding-left: 8px;
}

.basket__button{
  display: flex;
}

.basket__count{
  background-color: @main-color;
  height: 25px;
  width: 25px;
  align-self: center;
  margin-right: 10px;
  border-radius: 12.5px;
  text-align: center;
  line-height:1.6;
  color: @back-color;
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  font-size: 14px;
}

.basket__link{
  -webkit-tap-highlight-color: @main-color;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 37px;
  width: 47px;
  padding-right: 3px;
  background-color: @main-color;
  color: @gray-color;
  margin-right: 0;
}

.basket__link:hover{
  background-color: @main-color;
  color: @back-color;
}

.basket__link:active{
  background-color: @main-color;
  color: @orange_color;
}

.basket__link:focus{
  background-color: @main-color;
  color: @orange_color;
}

.basket__link:disabled{
  background-color: @main-color;
  color: @dark-color;
}

.icon_basket{
  display: flex;
  font-size: 29px;
}