@font-face {
  font-family:"Ubuntu-Light";
  src:url("../font/Ubuntu-Light.woff2") format("woff2"),url("../font/Ubuntu-Light.woff") format("woff"),url("../font/Ubuntu-Light.ttf") format("ttf");
  font-style:normal;font-weight:400;
}
@font-face {
  font-family:"Ubuntu-Medium";
  src:url("../font/Ubuntu-Medium.woff2") format("woff2"),url("../font/Ubuntu-Medium.woff") format("woff"),url("../font/Ubuntu-Medium.ttf") format("ttf");
  font-style:normal;font-weight:400;
}
@import "reset";
@import "variable";
@import "common_style";
@import "topMenu";
@import "header_center";
@import "main_menu";
@import "aside";
@import "slick";
@import "slick-theme";

//main content
.main__content{
  display: flex;
  flex-direction: row-reverse;
  justify-content: flex-end;
  max-width: 1280px;
  padding: 0 @interval;
  margin: auto;
  position: relative;
}

.main__gray-back{
  background-color: @gray-color;
  width: 100%;
  height: 90px;
  top: 0;
  left: 0;
  position: absolute;
  z-index: -1;
}
//slider
.slide__link{
  -webkit-tap-highlight-color: transparent;
  margin-right: 0;
}

.slide__link:link{
  background-color: transparent;
}

.slide__link:visited{
  background-color: transparent;
}

.slide__link:hover{
  background-color: transparent;
}

.slide__link:focus{
  background-color: transparent;
}

.slide__link:active{
  background-color: transparent;
}

.slide{
  display: flex;
  position: relative;
}

.slide__text{
  position: absolute;
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  letter-spacing: 1px;
  color: @back-color;
  top:31%;
  right: 13%;
}

.slide__p{
  font-weight: 400;
  font-size: 17.5px;
  margin: 0;
}

.slide__headline{
  font-weight: 400;
  font-size: 55px;
  text-transform: uppercase;
  margin: 0;
  padding-bottom: 0;
}


//предложения
.proposals__content{
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  margin: 38px -@interval / 2 40px -@interval / 2;
}

.proposals__item{
  display: flex;
  width: 100%;
  align-content: stretch;
  margin: 0 @interval / 2;
}

.proposals__link{
  -webkit-tap-highlight-color: transparent;
  display: flex;
  flex-direction: row;
  margin-right: 0;
  width: 100%;
  height: 100%;
  padding: 20px 19px;
  color: @main-color;
  border: 1px solid @main-color;
}

.proposals__link:link{
  background-color: transparent;
  border: 1px solid @main-color;
}

.proposals__link:visited{
  background-color: transparent;
  border: 1px solid @main-color;
}

.proposals__link:hover{
  background-color: transparent;
  border: 1px solid @orange_color;
}

.proposals__link:focus{
  background-color: transparent;
  border: 1px solid @orange_color;
}

.proposals__link:active{
  background-color: transparent;
  border: 1px solid @orange_color;
}

.proposals__title{
  display: flex;
  flex-direction: column;
}

.icon-kard, .icon-hammer{
  font-size: 38px;
  margin-right: 15px;
}

.proposals__headline{
  color: @dark-color;
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  margin-bottom: 0;
  margin-top: 4px;
  text-transform: uppercase;
}

.proposals__text{
  margin: 0 0 5px 0;
  color: @dark-color;
}

//Новинки
.section__main{
  display: flex;
  flex-direction: column;
}

.section-headline{
  border-top: 1px solid @gray-color;
  margin-bottom: 17px;
}

.section__headline{
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 18px;
  color: @dark-color;
}
.sections__item{
  display: flex;
  flex-direction: row;
  flex-wrap: nowrap;
  justify-content: space-between;
  margin: 0 -@interval / 2 70px -@interval / 2;
}

.section__new, .section__discounts{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  width: 100%;
  margin: 0 @interval / 2;
  position: relative;
  max-width: 212px;
}

.label{
  position: absolute;
  top: 0;
  left: 0;
  width: 54px;
  height: 54px;
  background-color: @orange_color;
  border-radius: 0 0 54px 0;
  font-family: 'Ubuntu-Medium', sans-serif;
  font-weight: 400;
  font-size: 16px;
  color: @back-color;
  padding: 8px 0 0 5px;
  z-index: 2;
}

.lable__discounts{
  background-color: @main-color;
  color: @gray-color;
}

.section-item__link{
  margin: 0;
}

.item__title{
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  font-size: 16px;
  text-transform: uppercase;
  text-align: center;
  margin-top: 19px;
  margin-bottom: 0;
}

.item__cost{
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  font-size: 14px;
  text-align: center;
  margin-top: 3px;
  margin-bottom: 24px;
}

.section__form{
  display: flex;
  justify-content: flex-start;
  margin-top: auto;
  width: 100%;
  height: 35px;
}

.basket-new__button, .basket-discounts__button{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24%;
  font-size: 25px;
  border-left: 1px solid @back-color;
  color: @back-color;
}

.new__button{
  background-color: @orange_color;
}

.discounts__button{
  background-color: @main-color;
  color: @gray-color;
}

.button__one-clik{
  -webkit-tap-highlight-color: @orange_color;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 76%;
  background-color: @orange_color;
  color: @back-color;
  font-family: 'Ubuntu-Light', sans-serif;
  font-weight: 400;
  font-size: 14px;
}

.basket-new__button:hover, .button__one-clik:hover{
  background-color: @orange_color;
  color: @main-color;
}

.basket-new__button:active, .button__one-clik:active{
  background-color: transparent;
  color: @main-color;
}

.basket-new__button:focus, .button__one-clik:focus{
  background-color: @orange_color;
  color: @main-color;
}

.basket-new__button:disabled, .button__one-clik:disabled{
  background-color: @orange_color;
  color: @dark-color;
}

.button__discounts-figure{
  -webkit-tap-highlight-color: @main-color;
  background-color: @main-color;
  color: @gray-color;
}

.basket-discounts__button:hover, .button__discounts-figure:hover{
  background-color: @main-color;
  color: @back-color;
}

.basket-discounts__button:active, .button__one-clik:active{
  background-color: @main-color;
  color: @orange_color;
}

.basket-discounts__button:focus, .button__one-clik:focus{
  background-color: @main-color;
  color: @orange_color;
}

.basket-discounts__button:disabled, .button__one-clik:disabled{
  background-color: @main-color;
  color: @dark-color;
}

//footer
.footer__main{
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-top: 20px;
  margin-bottom: 50px;
  position: relative;
}

.footer-logo__pict{
  color: @gray-color;
}

.footer__link{
  align-self: flex-start;
}

.footer-menu__conteiner{
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  border-top: 1px solid @gray-color;
  margin-top: 22px;
  margin-right: 30px;
  width: 20%;
  flex-wrap: nowrap;
  flex-grow: 1;
}

.footer__logo{
  border-top: 0;
  margin-top: 22px;
  margin-left: 0;
  flex: 0 4 40%;
}

.footer-menu__conteiner:last-child{
  margin-right: 0;
}

.footer-menu__title{
  font-family: Ubuntu-Light,sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  font-size: 16px;
  color: @gray-color;
}

.footer__menu{
  background-color: transparent;
  flex-direction: column;
  padding-left: 0;
}

.footer-menu__item{
  display: flex;
  justify-content: flex-start;
}

.footer-menu__link{
  padding: 5px 0;
  font-size: 14px;
}

.footer__address{
  padding: 5px 0;
  font-style: normal;
  font-size: 14px;
  margin: 0;
  color: @gray-color;
  font-family: Ubuntu-Light,sans-serif;
  font-weight: 400;
}

.copyright{
  display: flex;
  height: 50px;
  background-color: @black-color;
  margin: 0 -@interval;
  align-items: center;
  position: relative;
}

.copyright::after{
  content: '';
  display: block;
  height: 100%;
  width: 100vw;
  background-color: inherit;
  position:absolute;
  top: 0;
  margin: auto;
  left: -50vw;
  right: -50vw;
  z-index: -1;
}

.copyright__text{
  font-family: Ubuntu-Light,sans-serif;
  font-weight: 400;
  padding-left: @interval;
  color: @gray-color;
}


@media (max-width: 990px) {

  //header
  .header-main__connect{
    flex-direction: column;
  }

  .header-main-connect__tel, .header-main-connect__email{
    margin-top: 10px;
  }

  .form__search{
    display: flex;
    flex-direction: row;
    margin-top: 10px;
    padding-bottom: 30px;
  }

  .basket{
    margin-bottom: 30px;
  }

  .main__gray-back{
    height: 10px;
  }
  //aside
  .aside__baner{
    display: none;
  }

  .aside{
    width: 10%;
  }

  .submenu-tab{
    display: none;
  }

  .aside__item{
    border-bottom: 0;
  }

  .aside__title{
    display: none;
  }

  .menu__ul{
    margin-top: 40px;
  }

  .main{
    width: 90%;
  }
}

@media (max-width: 800px) {
  //main
  .main__content{
    flex-direction: column-reverse;
  }

  .main{
    width: 100%;
  }

  .main__slider{
    margin: 0 -@interval 0 -@interval;
    width: auto;
    margin-bottom: 20px;
  }

  .main__gray-back{
    display: none;
  }
  //aside
  .aside__menu{
    margin-bottom: 15px;
  }

  .menu__ul{
    display: flex;
    flex-direction: row;
    margin-top: 10px;
  }

  .icon_piano, .icon_trump, .icon_violin, .icon_drum, .icon_sheet_music, .icon_accessories{
    font-size: 27px;
    margin-right: 20px
  }
}


@media (max-width: 767px) {
  //header
  .main-logo{
    width: 230px;
  }

  .header-main__logo{
    width: auto;
    margin-left: 0;
    flex-grow: 0;
  }

  .footer-logo__pict{
    width: 190px;
  }

  .footer__link{

  }

  .header-main-center__content{
    flex-direction: column;
    justify-content: center;
  }

  .header-main__search{
    align-items: flex-end;
    align-self: flex-end;
    width: auto;
  }

  .header-main__connect{
    flex-direction: column;
    align-self: flex-end;
  }

  .header-main-connect__tel, .header-main-connect__email{
    margin-top: 10px;
    margin-right: 0;
    flex-direction: row-reverse;
    align-items: center;
    align-self: flex-end;
  }

  .form__search{
    width: 185px;
    padding-top: 10px;
    padding-bottom: 20px;
    align-self: flex-end;
  }

  .search__input{
    width: 137px;
  }

  .basket{
    margin-left: 0;
  }

  .header-main{
    flex-wrap: wrap;
  }

  .slick-arrow{
    display: none!important;
  }
}

@media (max-width: 754px) {

  .sections__item{
    flex-wrap: wrap;
    justify-content: center;
    margin: 0 -@interval / 2 20px -@interval / 2;
  }

  .section__new, .section__discounts{
    margin: 0 @interval / 2 50px @interval / 2;
  }

  .footer-menu__conteiner{
    width: auto;
    flex: 1 0 10%;
    margin-top: 75px;
  }

  .footer__logo{
    margin-top: -10px;
    position: absolute;
  }

  .footer-logo__pict{
    margin-right: 0;
    width: 160px;
  }
}

@media (max-width: 660px) {

  .proposals__content{
    flex-wrap: wrap;
    margin: 38px -@interval / 2 40px -@interval / 2;
  }

  .proposals__item{
    margin: 0 @interval / 2 @interval @interval / 2;
  }

  .proposals__item:last-child{
    margin: 0 @interval / 2 @interval / 2 @interval / 2;
  }
}

@media (max-width: 600px) {

  .slide__text{
    top:15%;
    right: 6%;
  }

  .slide__headline{
    font-size: 47px;
  }

  .slide__p{
    font-weight: 400;
    font-size: 16.5px;
  }

  .section__new, .section__discounts{
    max-width: 37.2%;
  }
}

@media (max-width: 551px) {
  .header__main-menu{
    align-items: flex-start;
    flex-direction: column;
    position: relative;
  }

  .menu__main {
    display: flex;
    position: absolute;
    flex-direction: column;
    align-items: flex-start;
    top: 45px;
    width: 100%;
    z-index: 2;
    padding: 0;
    overflow: hidden;
    height: 0;
  }

  .menu__item{
    margin-left: 30px;
  }

  .menu__item:first-child{
    margin-top: 5px;
  }

  .menu__item:last-child{
    margin-bottom: 20px;
  }

  .footer__menu{
    display: flex;
    position: relative;
    padding: 0 0 0 0;
    top: 0;
    height: auto;
  }

  .footer-menu__item{
    margin-left: 0;
  }

  .footer-menu__item:last-child{
    margin-bottom: 0;
  }

  .footer-menu__item:first-child{
    margin-bottom: 0;
  }

  .hide__menu {
    display: flex;
    align-self: flex-start;
  }

  .slick-dots{
    bottom: -33.6px;
  }

  .footer-menu__conteiner:nth-child(3){
    display: none;
  }

  .footer-menu__conteiner{
    margin-top: 100px;
  }
}

@media (max-width: 500px) {

  //header
  .header__main{
    flex-direction: column;
    align-items: center;
  }

  .header-main__logo{
    width: auto;
    margin-left: 0;
    margin-right: 0;
    flex-grow: 0;
    padding-top: 40px;
  }
  //aside
  .span{
    display: none;
  }
}

@media (max-width: 450px) {

  .slide__text{
    top:19%;
    right: 6%;
  }

  .slide__headline{
    font-size: 39px;
    text-align: right;
  }

  .slide__p{
    font-weight: 400;
    font-size: 15px;
  }

  .section__new, .section__discounts{
    max-width: 100%;
    border: 1px solid @gray-color;
  }
}

@media (max-width: 360px) {

  .slide__text{
    top:27%;
    right: 9%;
  }

  .slide__headline{
    font-size: 32px;
  }

  .slide__p{
    display: none;
  }
}

